
export default {
    name: 'zy-form-group-row',
    inject: ['model', "_colSpan", "labelWidth"],
    props: {
        //
        label: String,
        //
        prop: String,
        // (3) +  = colSpan,
        //  colSpan123prop9
        // _colSpan,ZyFormGroup(24/)
        // colSpan_colSpan,colSpan
        colSpan: {
            type: Number
        }
    },
    /* eslint-disable */
    render(h, ctx) {
        // 
        const spanWidth = this.colSpan > 0 ? this.colSpan : this._colSpan
        const style = {
            class: 'zy-form-group-row',
            attrs: {
                colspan: spanWidth // 3
            }
        }
        const title = h('div', {
            class: 'zy-form-group-row-head',
            style: {
                width: this.labelWidth
            }
        }, [this.label])
        //
        if (!this.$scopedSlots.default) {
            let prop = this.prop
            let value = this.model[prop]
            //,
            if (prop.indexOf('.') != -1) {
                const keys = prop.split('.')
                value = this.model
                keys.forEach(key => {
                    value = value[key]
                });
            }
            const label = h('div', {
                class: 'zy-form-gtoup-row-content'
            }, [value])
            const box = h('div', {
                class: 'zy-form-group-row-box'
            }, [title, label])
            return h('td', style, [box])
        }
        //
        const label = h('span', {
            class: 'zy-form-gtoup-row-content'
        }, [this.$scopedSlots.default({ props: this.$props, model: this.model })])
        const box = h('div', {
            class: 'zy-form-group-row-box'
        }, [
            title,
            label
        ])
        return h('td', style, [box])
    }
}